
.footer-card-left .name-collection {
  white-space  : nowrap;
  color        : rgb(153, 153, 153);
  font-size    : 0.81rem;
  overflow     : hidden;
  text-overflow: ellipsis;
}

.footer-card-left .name-nft {
  overflow     : hidden;
  white-space  : nowrap;
  text-overflow: ellipsis;
  color        : rgb(0, 0, 0.80);
  font-size    : 16px;
}

.card-nft {
  .price-nft {
    position     : absolute;
    top          : -1px;
    right        : -1px;
    background   : linear-gradient(165.02deg, #6080B4 65%, rgba(255, 0, 134, 0) 118.17%);
    border-radius: 0 5px 0 5px;
    color        : white !important;
    padding      : 0 0.5rem 0 0.5rem;
  }

  .attribs-nft {
    @extend .price-nft;
    right        : auto;
    left         : -1px;
    border-radius: 5px 0 5px 0;
  }
}


.footer-card-right .title-price {
  white-space: nowrap;
  color      : rgb(153, 153, 153);
  font-size  : 0.81rem;
}

.price-nft b {
  font-size: 0.68rem;
}

.tick-and-available {
  margin-top: 0.5rem;
}

.tick-and-available-left img {
  display: inline-block;
  width  : 1.2rem;
  height : 1.2rem;
}

.card-nft .ant-card-body {
  padding: 0.5rem;
}

.wrap-cover {
  width        : 100%;
  height       : 100%;
  overflow     : hidden;
  border-radius: 0.5rem;
}

.NFTResource-Wrapper {
  width  : 100%;
  height : 100%;
  padding: 0 10px;
}

.display-resource-nft {
  border-radius: 2px 2px 0 0;
  width        : 262px;
  height       : 262px;
  object-fit   : contain;
  position     : relative;
}

.display-resource-nft:hover {
  transform : scale(1.03);
  transition: all 0.3s ease-in-out 0s;
}

.page-profile .footer-card-left {}

.list-nft-owner .fill-width {
  width: 100%;
}

.card-nft .blurred-background {
  filter             : blur(8px);
  -webkit-filter     : blur(8px);
  background-position: center;
  background-repeat  : no-repeat;
  background-size    : cover;
  width              : 100%;
  height             : 20vh;
  position           : absolute;
  z-index            : 1;
}

.card-nft-content-loader {
  opacity: 0.65;

  img,
  .name-collection,
  .name-nft {
    // taken from ant Skeleton
    animation      : ant-skeleton-loading 1.4s ease infinite;
    background     : linear-gradient(90deg, rgba(190, 190, 190, .2) 25%, rgba(129, 129, 129, .24) 37%, rgba(190, 190, 190, .2) 63%);
    background-size: 400% 100%;
  }

  .footer-card-left {
    width: 100% !important;

    .name-collection,
    .name-nft {
      border-radius: 2px;
    }
  }

  .wrap-cover {
    border-radius: 0px;
  }
}