html[data-theme='light'] {
  --color                 : rgb(5, 5, 5);
  --background-color      : rgb(255, 255, 255);
  --nft-background        : #fafafa;
  --input-background      : #ededed;
  --input-background-white: white;
}

[data-theme='light'] .ant-card-bordered {
  border: 1px solid #d9d9d9c9 !important;
}

[data-theme='light'] .menu-bar,
.background-mode {
  background-color: var(--background-color) !important;
}

[data-theme='light'] .ant-modal-header {
  background-color: #fff !important;
}

[data-theme='light'] .dropzone {
  color    : rgba(45, 46, 46, 0.35);
  // border: 1px solid #ff0086;
}

// [data-theme='light'] .choose>.active {
//   border: 1px solid #ff0086 !important;
// }

[data-theme='light'] {
  .textmode.price-nft {
    color: white !important;
  }
}

[data-theme='light'] .textmode,
.search-style,
.ant-select-suffix>svg,
.ant-form-item-required {
  color: rgba(0, 0, 0, 0.80) !important;
}

[data-theme='light'] .ant-menu-vertical>.ant-menu-item-selected {
  background-color: #e6f7ff !important;
}

[data-theme='light'] .ant-menu-submenu-popup>.ant-menu,
.ant-radio-button-wrapper {
  background-color: #fff !important;
}

[data-theme='light'] .dropdown_network_header {
  background-color: #ffffff;
}

[data-theme='light'] .dropdown-select-chain {
  background-color: #ffffff !important;
}

[data-theme='light'] .dropdown-select-chain img {
  box-shadow: 0 0 0 2px #dfe8f9;
}

[data-theme='light'] .sidebar-menu-network-icon {
  box-shadow: 0 0 0 2px #dfe8f9;
}